.navbar {
  width: 100%;
  background: #2e2c2cff;
  padding: 12px 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
}

.navbar .brand {
  font-size: 28px;   /* increased from 25px */
  font-weight: 700;
}

.navbar .right-section {
  display: flex;
  align-items: center;
  gap: 20px;
}

.navbar .role-text {
  font-size: 16px;   /* was 15px */
}

.navbar .role-label {
  font-weight: 600;
  font-size: 18px;   /* was 16px */
  background: rgba(255, 255, 255, 0.2);
  padding: 6px 12px;
  border-radius: 12px;
}

.navbar .logout-btn {
  background: transparent;
  border: 1px solid #fff;
  color: #fff;
  padding: 6px 12px;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.2s ease-in-out;
}

.navbar .logout-btn:hover {
  background: rgba(255, 255, 255, 0.15);
}
